<template>
  <div class="ui-alert" :class="classList">
    <span class="ui-alert-message">
      <strong v-if="title"> {{ title }} </strong>
      <slot></slot>
    </span>
    <span class="ui-alert-close-btn"></span>
  </div>
</template>

<script>
export default {
  computed: {
    classList() {
      return {
        'ui-alert-md': this.size === 'md',
        'ui-alert-xs': this.size === 'xs',
        'ui-alert-default': this.color === 'default',
        'ui-alert-primary': this.color === 'primary',
        'ui-alert-success': this.color === 'success',
        'ui-alert-warning': this.color === 'warning',
        'ui-alert-danger': this.color === 'danger',
        'ui-alert-icon-warning': this.icon === 'warning',
        'ui-alert-icon-danger': this.icon === 'danger',
        'ui-alert-icon-info': this.icon === 'info',
        'ui-alert-text-center': this.center,
        'ui-alert-inline': this.inline,
      };
    },
  },
  props: {
    title: {
      type: String,
      default: 'Внимание!',
    },
    size: {
      type: String,
      default: 'md',
    },
    color: {
      type: String,
      default: 'default',
    },
    icon: {
      type: String,
      default: '',
    },
    center: {
      type: Boolean,
      default: false,
    },
    inline: {
      type: Boolean,
      default: false,
    },
  },
  name: 'bx-alert',
};
</script>
