<template>
  <example-table name="bx-input-file">
    <bx-input-file
      :type="props.type"
      :multiple="props.multiple"
      :disabled="props.disabled"
    >{{ slot.default }}</bx-input-file>
    <template #code>{{ markup }}</template>
    <template #params>
      <label>
        slot.default
        <input type="text" v-model="slot.default">
      </label>
      <label>
        props.type
        <select v-model="props.type">
          <option
            v-for="item in settings.types"
            :value="item"
          >{{ item }}</option>
        </select>
      </label>
      <label>
        <input type="checkbox" v-model="props.multiple">
        props.multiple
      </label>
      <label>
        <input type="checkbox" v-model="props.disabled">
        props.disabled
      </label>
    </template>
  </example-table>
</template>

<script>
import ExampleTable from './Table.vue';
import BxInputFile from '../BxInputFile.vue';

export default {
  computed: {
    markup() {
      return `
<bx-input-file
  type="${this.props.type}"
  :multiple="${this.props.multiple}"
  :disabled="${this.props.disabled}"
>${this.slot.default}</bx-input-file>
      `;
    },
  },
  data() {
    return {
      slot: {
        default: '',
      },
      props: {
        type: '',
        multiple: false,
        disabled: false,
      },
      settings: {
        types: ['', 'button', 'link'],
      },
    };
  },
  components: {
    ExampleTable,
    BxInputFile,
  },
  name: 'example-input-file',
};
</script>
