<template>
  <label v-if="type === 'button'" class="ui-ctl ui-ctl-file-btn">
    <input
      class="ui-ctl-element"
      type="file"
      :multiple="multiple"
      :disabled="disabled"
      @change="onChange"
    >
    <div class="ui-ctl-label-text">
      <slot></slot>
      <span v-if="!$slots.default[0].text">Загрузить файл или картинку</span>
    </div>
  </label>
  <label v-else-if="type === 'link'" class="ui-ctl ui-ctl-file-link">
    <input
      class="ui-ctl-element"
      type="file"
      :multiple="multiple"
      :disabled="disabled"
      @change="onChange"
    >
    <div class="ui-ctl-label-text">
      <slot></slot>
      <span v-if="!$slots.default[0].text">Загрузить файл или картинку</span>
    </div>
  </label>
  <label v-else class="ui-ctl ui-ctl-file-drop">
    <div class="ui-ctl-label-text">
      <slot></slot>
      <template v-if="!$slots.default[0].text">
        <span>Загрузить файл или картинку</span>
        <small>Перетащить с помощью drag'n'drop</small>
      </template>
    </div>
    <input
      class="ui-ctl-element"
      type="file"
      :title="title"
      :multiple="multiple"
      :disabled="disabled"
      @change="onChange"
    >
  </label>
</template>

<script>
export default {
  methods: {
    onChange(e) {
      for (const file of e.target.files) this.files.push(file);
      this.$emit('change', this.files);
    },
  },
  computed: {
    title() {
      if (this.files.length) return this.files.map((file) => file.name).join('\n');
      if (this.multiple) return 'Файлы не выбраны.';
      return 'Файл не выбран.';
    },
  },
  data() {
    return {
      files: [],
    };
  },
  props: {
    type: {
      type: String,
      default: '',
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  name: 'bx-input-file',
};
</script>

<style lang="stylus" scoped>
.ui-ctl-file-drop .ui-ctl-element
  display block
  position absolute
  top 0
  left 0
  width 100%
  height 100%
  opacity 0
  cursor pointer
</style>
